<template>
  <div id="app" class="no-touch">
      <el-menu :default-active="activeIndex" class="space-between center el-menu-demo" mode="horizontal" router>
        <el-menu-item index="1" class="loulan" route="/">
          <i class="logo"></i>
        </el-menu-item>
        <!--<el-menu-item index="1" route="/">首页</el-menu-item>-->
        <el-menu-item index="2" disabled>项目</el-menu-item>
        <el-menu-item index="3" disabled>实验室</el-menu-item>
        <el-menu-item index="4" route="about">关于</el-menu-item>
      </el-menu>
      <el-main>
        <router-view/>
      </el-main>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      activeIndex: '1'
    }
  },
  methods: {

  }
}
</script>

<style>
  html, body {
    margin: 0;
    padding: 0;
  }

  #app {
    font-family: 'PingFang SC', 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }

  .el-main{
    padding: 0;
  }

  .space-between{
    display: flex;
    justify-content: space-between;
  }

  .loulan {
    display: flex;
    justify-content:center;
    align-items:Center;
  }

  .logo {
    border: 2px solid #040627;
    position: absolute;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    background:url("./assets/logo.jpeg") no-repeat center;
    background-size: cover;
  }
</style>



