<template>
  <div class="main">
    <section class="section everyday">

      <div class="section-tiles-grid">
        <div class="content">
          <ul class="landingtiles">

            <li class="component newlandingtile show-tile"
                v-for="(item, index) in results"
                :key='item'
                v-bind:class="{'landingtile-hero':(index === 0), 'tile-head':(index === 1), 'landingtile-wide':!(index === 2)}">

              <a class="tile-link">
                <div class="tile-content" aria-hidden="true">

                  <div class="tile-media" dir="ltr">
                    <div class="image">
                      <div class="back-img image-asset image-asset-anim" v-if="index !== 2"
                           v-bind:class="{'back-img3':(index === 2)}"
                           v-bind:style="{backgroundImage: 'url(' + (item.image.rectangle) + ')'}">
                      </div>
                      <div class="back-img image-asset image-asset-anim" v-else
                           v-bind:class="{'back-img3':(index === 2)}"
                           v-bind:style="{backgroundImage: 'url(' + (item.image.square) + ')'}">
                      </div>
                    </div>
                  </div>

                  <div class="tile-content-text">
                    <div class="title-head">
                      <div class="category-eyebrow">
                        <span class="category-eyebrow__category category_release" v-bind:style="{color: item.tag.color, borderColor: item.tag.color}">{{ item.tag.text }}</span>
                      </div>
                      <p class="tile-content-headline">{{ item.title }}</p>
                      <p class="tile-content-description" v-if="index === 0">{{ item.summary }}</p>
                    </div>
                    <time class="tile-date icon icon-before icon-clock icon-category_release" :datetime='item.time'>{{ (item.time) | formatDate }}</time>
                  </div>

                  <div class="tile-subhead" v-if="!(index === 0)">
                    {{ item.summary }}
                  </div>

                </div>
              </a>

            </li>

          </ul>
        </div>
      </div>

    </section>
