<script>
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      results: []
    }
  },
  created () {
    axios
      .get('http://120.79.24.49:2022/blog')
      .then(data => {
        this.results = data.data
      })
      .catch(function (response) {
        console.log(response)
      })
  },
  filters: {
    formatDate: function (value) {
      let date = new Date(value)
      let y = date.getFullYear()
      let MM = date.getMonth() + 1
      MM = MM < 10 ? ('0' + MM) : MM
      let d = date.getDate()
      d = d < 10 ? ('0' + d) : d
      let h = date.getHours()
      h = h < 10 ? ('0' + h) : h
      let m = date.getMinutes()
      m = m < 10 ? ('0' + m) : m
      let s = date.getSeconds()
      s = s < 10 ? ('0' + s) : s
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s
    }
  }
}
